.quiz-container {
  font-family: Arial, sans-serif;
  padding: 20px;
  max-width: 800px;
  margin: auto;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 10px;
}

.quiz-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.quiz-title-input,
.question-input,
.answers-section input {
  width: 100%;
  padding: 10px;
  margin: 5px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.question-type-select {
  margin: 10px 0;
  padding: 10px;
}

.save-btn,
.add-question-btn {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.save-btn:hover,
.add-question-btn:hover {
  background-color: #0056b3;
}

.answers-section .answer {
  display: flex;
  gap: 10px;
  margin-bottom: 10px;
}

.answers-section .answer input {
  flex: 1;
}

button {
  cursor: pointer;
}

.question-container {
  background: rgb(255 255 255 / 1%);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 10px;
  transition: transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
}

.question-title {
  font-size: 1.2rem;
  color: #14035f;
}

.answers-container {
  margin-top: 20px;
}

.answer-option {
  padding: 20px;
  border: 2px solid #e0e0e0;
  background-color: #14035f;
  border-radius: 8px;
  transition: background-color 0.2s ease-in-out, border-color 0.2s ease-in-out;
  display: flex;
  align-items: start;
  color: #fff;
}

.answer-option:hover {
  border-color: #33cae5;
}

.answer-label {
  margin-left: 10px;
  font-size: 1rem;
  color: #333;
}

.form-check-input {
  width: 25px;
  height: 25px;
  cursor: pointer;
  accent-color: #33cae5;
  border-radius: 50px !important;
  flex-shrink: 0;
  margin-left: 0 !important;
  margin-right: 0 !important;
}
.form-check-input:checked {
  background-color: #33cae5;
}
.quiz-header {
  border-radius: 10px;
  padding: 20px 30px;
  transition: transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
}

.quiz-title {
