@media print {
  @page {
    size: A4;
    background: #080029 !important;

    /* Custom header */
    @top-center {
      display: none; /* Padding for better spacing */
    }

    /* Custom footer */
    @bottom-center {
      content: "Page " counter(page) " of " counter(pages);
      color: white; /* Footer text color */
      background: #080029; /* Footer background color */
      padding: 10px 0;
      font-size: 15px;
      text-align: end;
      margin-bottom: 15px;
    }
  }

  /* Ensure proper page breaks */
  .break-after-page {
    page-break-after: always;
  }

  /* Prevent breaking inside elements */
  .no-break,
  .text-content {
    page-break-inside: avoid;
  }

  /* Hide elements not needed for printing */
  .no-print {
    display: none !important;
  }

  /* Prevent the sidebar from being hidden */
  .sidebar {
    display: block !important;
  }
  .text-content {
    text-align: justify !important;
    page-break-inside: avoid; /* Avoid breaking paragraphs */
  }
  /* Ensure the correct background color in print mode */
  body,
  .back_print {
    background-color: #080029 !important;
  }

  /* Hide buttons during print */
  button.px-4.py-2 .text-white.rounded {
    display: none;
  }

  /* Ensure print-specific elements are visible */
  .print_name {
    display: block !important;
  }
}

