/* 🌟 Shepherd Tour - Modern Glassmorphism Popup */
.custom-shepherd-theme {
  border-radius: 24px; /* Smoother corners */
  background: rgba(51, 202, 229, 0.733); /* Light glass effect */
  backdrop-filter: blur(25px); /* Frosted glass depth */
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.3); /* Deeper shadow for pop */
  padding: 28px;
  font-family: "Poppins", sans-serif;
  color: #ffffff; /* White text for contrast */
  animation: slideIn 0.6s ease; /* Smooth entrance animation */
}

/* Slide-in Animation */
@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(50px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ✨ Title Styling */
.custom-shepherd-theme .shepherd-title {
  font-size: 24px; /* Larger and bolder */
  font-weight: 600;
  color: #33cae5; /* Vibrant title color */
  letter-spacing: 1.2px;
  text-transform: capitalize;
  display: flex;
  align-items: center;
  gap: 12px; /* Space for icons */
}

.shepherd-has-title .shepherd-content .shepherd-header {
  background: #111827 !important;
}

.shepherd-header {
  align-items: center;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
}

/* 🧾 Description/Text Styling */
.custom-shepherd-theme .shepherd-text {
  font-size: 18px; /* Larger body font */
  line-height: 1.8;
  color: #000; /* Softer white */
}

/* 🔘 Button Container */
.custom-shepherd-theme .shepherd-footer {
  display: flex;
  justify-content: space-between; /* Better button spacing */
  gap: 16px;
}

/* 🚀 General Button Style */
.custom-shepherd-theme .shepherd-button {
  padding: 12px 24px;
  border-radius: 18px;
  font-size: 16px;
  cursor: pointer;
  transition: transform 0.3s, background 0.4s;
}

/* ✅ Next Button (Interactive) */
.custom-shepherd-theme .shepherd-button:last-child {
  background: linear-gradient(135deg, #33cae5, #1b8fab);
  color: white;
  border: none;
}

.custom-shepherd-theme .shepherd-button:last-child:hover {
  transform: translateY(-2px) scale(1.1); /* Hover lift effect */
  background: linear-gradient(135deg, #1b8fab, #0f6678);
}

/* 🔙 Back Button */
.custom-shepherd-theme .shepherd-button:first-child {
  background: transparent;
  border: 1px solid #33cae5;
  color: #33cae5;
}

.custom-shepherd-theme .shepherd-button:first-child:hover {
  background: #33cae5;
  color: white;
  transform: scale(1.05);
}

/* 🌌 Dim Overlay */
.shepherd-modal-overlay-container {
  background: rgba(0, 0, 0, 0.75); /* Darker overlay */
}

.start-tour-btn {
  padding: 14px 25px;
  font-size: 17px;
  background: linear-gradient(135deg, #33cae5, #1b8fab);
  color: white;
  border: none;
  border-radius: 28px;
  cursor: pointer;
  transition: background 0.4s ease, transform 0.3s ease;
  box-shadow: 0 10px 30px rgba(51, 202, 229, 0.5);
  /* margin-bottom: 36px; */
  height: fit-content;
}
.start-tour-btn:hover {
  background: linear-gradient(135deg, #1b8fab, #0f6678);
}

@media (max-width: 768px) {
  .start-tour-btn {
    top: unset;
    bottom: 112px;
    left: 10px;
    width: max-content;
  }
}

/* 🌟 Pulse Animation */
@keyframes pulse {
  0% {
    box-shadow: 0 0 20px rgba(51, 202, 229, 0.6);
  }
  50% {
    box-shadow: 0 0 40px rgba(51, 202, 229, 0.8);
  }
  100% {
    box-shadow: 0 0 20px rgba(51, 202, 229, 0.6);
  }
}

.start-tour-btn.pulse {
  animation: pulse 2s infinite; /* Pulsating effect */
}
