/* HTML: <div class="loader"></div> */
.loader {
  width: 50px;
  padding: 8px;
  aspect-ratio: 1;
  border-radius: 50%;
  background: #33c6f4;
  --_m: conic-gradient(#0000 10%, #000), linear-gradient(#000 0 0) content-box;

  mask: var(--_m);
  mask-composite: subtract; /* Subtract inner circle */

  animation: spin 1s infinite linear; /* Infinite rotation */

  /* Centering the loader */
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* Rotation Animation */
@keyframes spin {
  to {
    transform: translate(-50%, -50%) rotate(1turn);
  }
}

.main_loader {
  background: #0000004f;
  height: 100%;
  width: 100%;
  z-index: 9898;
